import React from 'react';
import {
  mainBody,
  about,
  repos,
  leadership,
  skills,
  certifications
} from '../editable-stuff/config';
import MainBody from './home/MainBody.jsx';
import AboutMe from './home/AboutMe';
import Project from './home/Project';
import Skills from './home/Skills';
import TechSkills from './home/TechSkills.jsx';
import Leadership from './home/Leadership.jsx';
import Certifications from './home/Certifications';
import Blogs from './home/Blogs.jsx';

const Home = React.forwardRef((props, ref) => {
  return (
    <>
      <MainBody
        gradient={mainBody.gradientColors}
        title={`${mainBody.firstName} ${mainBody.middleName} ${mainBody.lastName}`}
        message={mainBody.message}
        jobtitle={mainBody.jobtitle}
        icons={mainBody.icons}
        ref={ref}
      />
      {about.show && (
        <AboutMe
          heading={about.heading}
          message={about.message}
          link={about.imageLink}
          imgSize={about.imageSize}
          resume={about.resume}
        />
      )}
      <TechSkills />
      {certifications.show && <Certifications certifications={certifications} />}
      {repos.show && (
        <Project
          heading={repos.heading}
          username={repos.gitHubUsername}
          length={repos.reposLength}
          specfic={repos.specificRepos}
        />
      )}
      {leadership.show && (
        <Leadership
          heading={leadership.heading}
          message={leadership.message}
          img={leadership.images}
          imageSize={leadership.imageSize}
        />
      )}
      <Blogs />
      {skills.show && (
        <Skills
          heading={skills.heading}
          hardSkills={skills.hardSkills}
          softSkills={skills.softSkills}
        />
      )}
    </>
  );
});

export default Home;
